import { TinyIcons } from 'assets/tiny-icons'
import ReplenishmentCard from 'components/Cards/ReplenishmentCard'
import Layout from 'components/Layout'
import SubHeader from 'components/SubHeader'
import { IReplenishment } from 'interfaces/Replenishment'
import React from 'react'
import { Discount, DiscountTooltip, Grid, Promocode, Text, TooltipItem, Wrapper } from './Styles'

export default function AccountReplenishment() {

	const items: IReplenishment[] = [
		{
			text: 'VISA/MasterCard/QIWI',
			condition: '(   )',
			banner: '/static/images/payment.png'
		},
		{
			text: 'Money',
			condition: '(   15)',
			banner: '/static/images/payment.png'
		},
		{
			text: 'QIWI',
			condition: '(   15)',
			banner: '/static/images/payment.png'
		},
		{
			text: 'VISA',
			condition: '( )',
			banner: '/static/images/payment.png'
		},
		{
			text: ' /',
			condition: '(   75)',
			banner: '/static/images/payment.png'
		},
	]

	return (
		<Layout
			leftElement={<div />}
			headerElement={
				<SubHeader>
					 
				</SubHeader>
			}
		>
			<Wrapper>
				<DiscountTooltip>
					<TooltipItem>
						<Discount>+5%</Discount>
						<Text>     100  999</Text>
					</TooltipItem>
					<TooltipItem>
						<Discount>+15%</Discount>
						<Text>     1000  10000</Text>
					</TooltipItem>
					<Promocode>
						<TinyIcons.DiscountStar />
						 
					</Promocode>
				</DiscountTooltip>
				<Grid>
					{items.map(({ banner, text, condition }, index) =>
						<ReplenishmentCard
							key={index}
							banner={banner}
							text={text}
							condition={condition}
						/>
					)}
				</Grid>
			</Wrapper>
		</Layout>
	)
}
